.ColorBox{
  width: 12rem;
  height: 30%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin-bottom: -5px;
}
.copy-button{
  width: 100px;
  height: 30px;
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  outline: none;
  background: rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  line-height: 30px;
  color: white;
  text-emphasis: none;
  border: none;
  text-transform: uppercase;
  opacity: 0;
  transition: opacity .5s linear;
}

.ColorBox:hover .copy-button{
  opacity: 1;
  cursor: pointer;
}
.box-content{
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 10px;
  color: black;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;

}

.see-more{
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  border: none;
  right:0;
  bottom: 0;
  width: 60px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
}

.copy-overlay{
  opacity: 0;
  z-index:0;
  width: 100%;
  height: 100%;
  transition: transform 0.9s ease-in-out;
  transform: scale(0.1);
}
.copy-overlay.show{
  position: absolute;
  opacity: 1;
  transform: scale(50);
  z-index:10;
}
.copy-msg{
  position: fixed;
  left: 0;
  right:0;
  top:0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  transform: scale(0.1);
  opacity: 0;
  color: white;
  flex-direction: column;
  
}

.copy-msg.show{
  opacity: 1;
  transform: scale(1.5);
  z-index:20;
  transition: all .3s ease-in-out;
  transition-delay: .3s;
  
}
.copy-msg h1{
  font-weight: 400;
  text-shadow: 1px 2px black;
  text-align: center;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  margin-bottom: 0;
  padding: 1rem;
  text-transform: uppercase;
}

.copy-msg p{
  font-size: 2rem;
  font-weight: 100;
}